<template>
    <div class="share">
        hello world
    </div>
</template>
<script>
export default {
  data () {
    return {
      a: 'name',
      isLogin: false
    }
  },
  beforeRouteEnter (to, from, next) {
    var isLogin = false
    // 判断是否为登录状态，如果不是，跳转登录页面，否则跳转到目标页
    if (isLogin) {
      next()
    } else {
      next('login')
    }
  }
}
</script>

<style>
</style>



