import Vue from 'vue'
import Router from 'vue-router'
import EntryMy from '@/components/EntryMy'
import Share from '@/components/sharingSystem/Share'
import Login from '@/components/login/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'EntryMy',
      component: EntryMy
    },
    {
      path: '/sharingSystem',
      name: 'Share',
      component: Share,
      meta: {
        auth: true
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})



