<template>
    <div class="login">
        <!-- <router-link to='-1'>返回</router-link> -->
        <back-head>
            <span class="piaowu">票务分享系统</span>
        </back-head>
        <article class="login-wrapper">
            <section class="user-info">
                <form class="input-group">
                    <div class="input-wraper">
                        <img class="input-before input-user" src="../../assets/images/sharing-login/sharing-login_03.png" alt="">
                        <input class="input-content" type="text" placeholder="请输入登录账号" v-model="userId">
                    </div>
                    <div class="input-wraper">
                        <img class="input-before input-password" src="../../assets/images/sharing-login/sharing-login_07.png" alt="">
                        <input class="input-content" type="text" placeholder="请输入密码" v-model="password">
                        <img class="input-after" src="../../assets/images/sharing-login/sharing-login_10.gif" alt="">
                    </div>
                </form>
            </section>
            <section class="login-button">
              <button @click="sub">登录</button>
            </section>
            <div ref="test"></div>
        </article>
    </div>
</template>
<script>
import BackHead from '../common/head/BackHead'
export default {
  name: 'Login',
  components: {
    BackHead
  },
  data () {
    return {
      userId: '',
      password: ''
    }
  },
  methods: {
    sub () {
      this.$refs.test.innerHTML = 'hello world'
    }
  }
}
</script>
<style lang="scss" scoped>
.piaowu {
  flex: 1;
  text-align: center;
  line-height: 0.56rem;
  padding-right: 0.48rem;
}
.login-wrapper {
  height: calc(100vh - 50px);
  background-color: #f2f2f2;
  //   输入框高100px,上边距35px
  .user-info {
    .input-group {
      overflow: hidden;
      .input-wraper {
        display: flex;
        height: 1rem;
        flex-direction: row;
        align-items: center;
        border-bottom: 1px solid #cccccc;
        width: 84%;
        margin: 0.35rem auto 0px;
        .input-before {
          margin: 0 0.4rem;
        }
        .input-user {
          width: 0.3rem;
        }
        .input-password {
          width: 0.32rem;
          height: 0.44rem;
        }
        .input-after {
          width: 0.4rem;
          height: 0.16rem;
        }
        .input-content {
          flex: 1;
          height: 100%;
          border: none;
          outline: none;
          padding-left: 0.2rem;
          font-size: 0.16rem;
          border: none;
          background-color: #f2f2f2;
          margin-bottom: 0px;
        }
        .input-content::-webkit-input-placeholder {
          color: #dddddd;
        }
      }
    }
  }
  .login-button {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top: 0.65rem;
        button {
          width: 84%;
          height: 0.8rem;
          background: #b6b6b6;
          color: #fefefe;
          outline: none;
          border: none;
          border-radius: 5px;
          margin: 0 auto;
          font-size: 0.36rem;
        }
      }
}
</style>



